import BaseAxios from "./_BaseAxios.js";
import ResHelper from "../_ResponseHelper.js"
import axios from "axios";

/**
 * 
 * GET /taskGroup/getGroupById
 * ID16559787
 * https://www.apifox.cn/web/project/838613/apis/api-16559787
 * @param {Number} groupId id
 * @returns {axios}
 */
function getGroupById({
    groupId
}) {

    return BaseAxios.get(`/taskGroup/getGroupById`, {
        params: {
            groupId
        },
    }).then(ResHelper.handler);
}


/**
 * Groupand 
 * POST /taskGroup/getGroups
 * ID16559807
 * https://www.apifox.cn/web/project/838613/apis/api-16559807
 * @param {string|null} keyWords 
 * @param {number} startPage 
 * @param {number} maxPage 
 * @param {string|null} startTime 
 * @param {string|null} endTime 
 * @param {'asc'|'desc'} sort 
 * @returns {axios}
 */
function getGroups({
    keyWords = null,
    startPage = 1,
    maxPage = 5,
    startTime = null,
    endTime = null,
    sort = 'asc'
}) {
    return BaseAxios({
        url: `/taskGroup/getGroups`,
        method: "post",
        data: {
            keyWords,
            startPage,
            maxPage,
            startTime,
            endTime,
            sort
        },
    }).then(ResHelper.handler);
}

