import File from "@/entity/Rbac/File";

class Order {
    /**
     * 
     * @param {Number} id id
     * @param {'complete'|'incomplete'} status :complete ,incomplete 
     * @param {Number} fee 
     * @param {string} title 
     * @param {string} serialNumber 
     * @param {string} createTime 
     * @param {string} url 
     * @param {string} size mb
     * @param {string} resolution 
     * @param {Number} taskId id
     * @param {string} result 
     * @param {string} fileName 
     * @param {Array.<File>} files 
     */
    constructor({
                    id,
                    status,
                    fee,
                    title,
                    serialNumber,
                    createTime,
                    url,
                    size,
                    resolution,
                    taskId,
                    result,
                    fileName,
                    files = []
                }) {
        // if (files.length > 0 && !(files[0] instanceof File)) {
        //     throw new Error('filesFile');
        // }
        this.id = id;
        this.status = status;
        this.fee = fee;
        this.title = title;
        this.serialNumber = serialNumber;
        this.createTime = createTime;
        this.url = url;
        this.size = size;
        this.resolution = resolution;
        this.taskId = taskId;
        this.resolution = resolution;
        this.result = result;
        this.fileName = fileName;
        this.files = files;
    }
}

export default Order;
