class File {
    /**
     * 
     * @param {Number} id id
     * @param {string} name 
     * @param {Number} taskId id
     * @param {string} size 
     * @param {string} createTime 
     * @param {Number} orderId idid
     */
    constructor({
                    id,
                    name,
                    taskId,
                    size,
                    createTime,
                    orderId
                }) {
        this.id = id;
        this.name = name;
        this.taskId = taskId;
        this.size = size;
        this.createTime = new Date(createTime);
        this.orderId = orderId;
    }
}

export default File;