/**
 * 
 * @param {Number} size Byte
 * @param {Number} pointLength 
 * @param {Array} units 
 *  1M
 */
function formatFileSize(
  size,
  pointLength = 2,
  units = ["B", "K", "M", "G", "TB"]
) {
  // 
  let unit;
  while ((unit = units.shift()) && size > 1024) {
    size = size / 1024;
  }
  return (unit === "B" ? size : size.toFixed(pointLength)) + unit;
}

export default {
  formatFileSize
};