class PageResult {
    /** 
     * 
     * @param {Array.<Object>} records 
     * @param {Number} total 
     * @param {Number} size 
     * @param {Number} current 
     * @param {Number} pages 
     */
    constructor(records, total, size, current, pages) {
        this.records = records;
        this.total = total;
        this.size = size;
        this.current = current;
        this.pages = pages;
    }
}

export default PageResult;